.order__success * {
	font-family: "Montserrat";
}

.order__success {
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
}

.order__success-back-link {
	font-size: small;
	text-decoration: underline;
	position: absolute;
	left: 10px;
	top: 80px;
}
.order__success-container {
	position: relative;
	width: 80%;
	max-width: 900px;
	background: white;
	height: 100%;
	min-height: 400px;
	display: flex;
	align-items: center;
	flex-direction: column;
	padding: 20px 0 10px 0;
	margin: 10px;
	border-radius: 10px;
}

.order__success-container * {
	text-align: center;
}

.foundOrder__page-customerInfo {
	width: 100%;
}

/* .foundOrder__page-customerInfo-text {
	flex-wrap: wrap;
	line-height: 1.4;
}
.foundOrder__page-customerInfo-text > p {
	flex: 1 1 45%;
} */

.foundOrder__page-order {
	margin-top: 10px;
	font-weight: 600;
}

.foundOrder__page-basket {
	width: 90%;
	margin: 30px auto 0 auto;
	border: 1px solid grey;
}

.foundOrder__page-basket td {
	border-top: 1px solid grey;
	border-left: 1px solid grey;
}
.foundOrder__page-basket td:first-child {
	border-left: none;
}

.foundOrder__page-basket * {
	text-align: center;
}

.foundOrder__page-table-total > td {
	font-size: 1.2rem;
}

.foundOrder__page-total {
	text-align: right;
	margin: 0 50px 20px 20px;
}

.foundOrder__page-total > span {
	margin-left: 15px;
	font-size: 1.5rem;
}
.foundOrder__page-delivery {
	text-align: right;
	margin: 25px 50px 5px 20px;
	font-size: small;
	font-weight: bold;
}

.foundOrder__page-delivery > span {
	margin-left: 15px;
	font-size: 1.1rem;
}

.inconvenience-msg {
	position: absolute;
	right: 10px;
	bottom: 3px;
	font-size: 14px;
}

.order__info-select {
	color: white;
	border: none;
	padding: 5px 8px;
	border-radius: 8px;
	margin-left: 12px;
	font-size: medium;
}

@media only screen and (max-width: 700px) {
	.order__success-container {
		width: 90%;
	}
}

@media only screen and (max-width: 600px) {
	.order__success-container {
		width: 100%;
	}
}

@media only screen and (max-width: 550px) {
	.foundOrder__page-basket {
		width: 98%;
	}
}
